import card from "@/components/common/card.vue"
import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import { mapMutations, mapState } from 'vuex'
export default {
	components: {
		card,
		uniListItem
	},
	data() {
		return {
			ctrl:[{
					label:"账号管理",
					value:[
						{ title:"个人资料",path:"" },
						{ title:"收货地址",path:"" },
					]
				}],
			list:[
				{
					label:"关于",
					value:[
						{ title:"关于商城",path:"about" },
						{ title:"意见反馈",path:"" },
						{ title:"协议规则",path:"" },
						{ title:"资质证件",path:"" },
						{ title:"用户协议",path:"" },
						{ title:"隐私政策",path:"" },
					]
				},
			]
		}
	},
	computed: {
		...mapState({
			loginStatus: state => state.user.loginStatus
		})
	},
	methods: {
		...mapMutations(['logout']),
		navigate(path){
			if (!path) return;
			uni.navigateTo({
				url: `/pages/${path}/${path}`
			});
		},
		// 退出登录
		logoutEvent() {
			this.$H.post('/logout',{},{
				token: false,
				toast: false
			}).then(res => {
				this.logout()
				uni.showToast({
					title: '退出成功',
					icon: 'none'
				})
				uni:uni.navigateBack({
					delta: 1
				})
			})
		}
	}
}
